@import '../../css/_variables.scss';

.login
{
  display: flex;

  width: 100vw;
  height: 100dvh;

  @include anim_fade_in;

  &__form
  {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 48px;

    width: $menu-w;
    height: 100dvh;
    padding-top: 64px;

    color: #fff;
    background-color: $bg-blue-mid;
  }

  &__logo
  {
    max-width: 320px;
    margin-bottom: 64px;
  }
  
  &__inputs
  {
    display: flex;
    flex-direction: column;
    gap: 16px;
    
    .login__input
    { 
      position: relative; 
    }

    .fa-envelope, .fa-key
    {
      position: absolute;
      top: 12px;
      left: 12px;

      font-size: 1.1rem;
    }

    input
    {
      display: block;

      width: 320px;
      padding: 12px 16px 12px 40px;
      border-radius: $bd-rad-small;

      color: $white-light;
      background-color: $bg-blue-light;

      &::placeholder
      { color: #c0c0c0; }
    }
  }

  &__auth
  {
    width: 320px;
    padding: 12px 16px;
    border-radius: $bd-rad-small;

    color: $white-light;
    background-color: $blue-dark;

    font-size: 1.2rem;

    &:hover
    { background-color: $blue-light; }
  }

  &__background
  {
    display: grid;
    place-items: center;

    width: calc(100% - $menu-w);
    height: 100dvh;

    background: url('/public/img/credentials__bg--dark_theme.png') center center/cover;
  }
}

@media screen and (max-width: 720px) 
{
  .login__form
  { width: 100vw; }

  .login__logo, .login__auth, input
  { max-width: 280px; }

  .login__background
  { display: none; }
}